.about {
  background-image: url(../images/about/milk-bg_320px_1x-min.png);

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/about/milk-bg_320px_2x-min.png);
  }
  @media screen and (min-width: 480px) {
    background-image: url(../images/about/milk-bg_320px_1x-min.png);
  }
  @media (min-device-pixel-ratio: 2) and (min-width: 480px) and (max-width: 767px),
    (min-resolution: 192dpi) and (min-width: 480px) and (max-width: 767px),
    (min-resolution: 2dppx) and (min-width: 480px) and (max-width: 767px) {
    background-image: url(../images/about/milk-bg_320px_2x-min.png);
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images/about/milk-bg_1280px_1x-min.png);
  }
  @media (min-device-pixel-ratio: 2) and (min-width: 1280px),
    (min-resolution: 192dpi) and (min-width: 1280px),
    (min-resolution: 2dppx) and (min-width: 1280px) {
    background-image: url(../images/about/milk-bg_1280px_2x-min.png);
  }

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
